import React from "react";
import s from './../Desc.module.css';
import Avatar from './Shurik.png';
import AvatarSec from './ShurikSec.png';

const Shurik=() =>{
	return (
	<div>
		<div className={s.image}>

				<img src={Avatar}/>
			
			<div className={s.overlay}>
						<div className={s.text}>
							<img src={AvatarSec}/>
						</div>
			</div>


		</div>
		<div className={s.name}>
			Шурик Михалев
		</div>
		<div className={s.description}>
			<p>Центурион</p>
			<ul>
  <li>Руководитель игровой студии, геймдизайнер</li>
  <li>Управление персоналом</li>
  <li>3D моделирование в Blender</li>
  <li>Анимация и спецэффекты в Blender и After Effects</li>
  <li>Композитор-звукоинженер</li>
			</ul>
		</div>
	</div>
	)
}

export default Shurik