import React from "react";
import s from './../Desc.module.css';
import Avatar from './Pavlov.png';
import AvatarSec from './PavlovSec.png';

const Pavlov=() =>{
	return (
	<div>
		<div className={s.image}>

				<img src={Avatar}/>
			
			<div className={s.overlay}>
						<div className={s.text}>
							<img src={AvatarSec}/>
						</div>
			</div>


		</div>
		<div className={s.name}>
			Алексей Павлов
		</div>
		<div className={s.description}>
			<p>Легат</p>
			Кандидат технических наук, специалист в области системного анализа и кибернетики, С/С++ разработчик, базы данных, алгоритмы, аналитик.

		</div>
	</div>
	)
}

export default Pavlov